@import url(https://maxcdn.bootstrapcdn.com/font-awesome/4.6.3/css/font-awesome.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #c78b38;
}

.textField {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 35vw;
  height: 35vh;
  margin: 2.5% 0;
  background-color: #c5c5c5;
  border: 2px solid black;
}

.textField #editor {
  width: 35vw;
  height: 35vh;
}

.liveView {
  width: 75%;
  min-height: 75vh;
  margin-bottom: 5%;
  border: 5px dotted black;
}

.liveView .bar {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 4vh;
  background-color: black;
  color: white;
  font-size: 22px;
}

.liveView #preview {
  width: 75%;
}
/*# sourceMappingURL=App.css.map */
.ToolBar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.ToolBar > * {
  padding: 5px 15px;
}

.ToolBar > .main {
  display: flex;
  justify-content: space-between;
  width: 10%;
  /* background-color: red; */
}

.ToolBar > .usefull {
  display: flex;
  justify-content: space-between;
  width: 30%;
  /* background-color: blue; */
}

.ToolBar > .lists {
  display: flex;
  justify-content: space-between;
  width: 10%;
  /* background-color: pink; */
}

.main > *,
.usefull > *,
.lists > * {
  cursor: pointer;
}

.expand {
  margin-left: 50px;
}

.expand:hover {
  color: red;
}
/*# sourceMappingURL=ToolBar.css.map */
